:root {
  --clr-txt-1: hsla(0, 0%, 0%, 1);
  --clr-txt-2: hsla(0, 0%, 48%, 1);
  --clr-neutral-1: hsla(0, 0%, 100%, 1);
  --clr-neutral-2: hsla(0, 0%, 98%, 1);
  --clr-neutral-3: hsla(0, 0%, 0%, 0.15)
  --clr-neutral-4: hsla(0, 0%, 0%, 0.05);
  --clr-neutral-5: hsla(0, 0%, 18%, .1);
  --clr-neutral-6: hsla(0, 0%, 18%, .05);
  --clr-accent: hsla(176, 50%, 47%, 1);
  --clr-accent-dark: hsla(176, 72%, 28%, 1);
  --clr-accent-disable: hsla(0, 0%, 18%, 1);
  --clr-border: hsla(0, 0%, 18%, .1);
  --clr-menu-bg-1: hsla(0, 0%, 0%, 1);
  --clr-menu-bg-2: hsla(0, 0%, 0%, 0);
  --clr-btn-hover: hsla(0, 0%, 44%, 1);
  --ff-commissioner: Commissioner, sans-serif;
}

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h1, h2, h3 {
  color: var(--clr-txt-1);
  font-weight: 700;
}

h1 {
  font-size: 1.25rem;
  line-height: 1.223;
}
@media (min-width: 75rem) {
  h1 {
    font-size: 1.75rem;
    line-height: 1.1514285714;
  }
}

h2 {
  font-size: 1.125rem;
  line-height: 1.2222222222;
}
@media (min-width: 75rem) {
  h2 {
    font-size: 1.25rem;
    line-height: 1.223;
  }
}

h3 {
  font-size: 0.875rem;
  line-height: 1.2228571429;
}
@media (min-width: 75rem) {
  h3 {
    font-size: 1.125rem;
    line-height: 1.2222222222;
  }
}

a {
  text-decoration: none;
}

body {
  font-family: var(--ff-commissioner);
  color: var(--clr-txt-2);
  font-size: 0.875rem;
  line-height: 1.7142857143;
}
@media (min-width: 75rem) {
  body {
    font-size: 1rem;
    line-height: 1.875;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
#bar1,
#bar2,
#bar3 {
  transition: all 250ms ease;
}

.navbar.open #bar2 {
  opacity: 0;
}
.navbar.open #bar1 {
  transform-origin: -1px 3px;
  transform: rotate(45deg);
}
.navbar.open #bar3 {
  transform: rotate(-45deg);
  transform-origin: 1px 13px;
}

.container {
  width: calc(100% - 3rem);
  margin-inline: auto;
}
.container--header {
  max-width: 69.375rem;
}
.container--main {
  max-width: 45.625rem;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 2rem;
}
@media (min-width: 45rem) {
  .navbar {
    padding-block: 3rem;
  }
}
.navbar.open .navbar__menu {
  display: block;
  animation: fade-in 300ms forwards;
}
@media (min-width: 45rem) {
  .navbar.open .navbar__menu {
    display: flex;
  }
}
.navbar.open .navbar__overlay {
  display: block;
  animation: fade-in 250ms forwards;
}
@media (min-width: 45rem) {
  .navbar.open .navbar__overlay {
    display: none;
  }
}
.navbar__toggler {
  background: transparent;
  border: none;
  cursor: pointer;
  z-index: 99;
}
@media (min-width: 45rem) {
  .navbar__toggler {
    display: none;
  }
}
.navbar__menu {
  display: none;
  list-style: none;
  position: absolute;
  top: 5.5rem;
  right: 1.5rem;
  left: 1.5rem;
  background-color: var(--clr-neutral-1);
  border-radius: 0.5rem;
  z-index: 99;
}
@media (min-width: 45rem) {
  .navbar__menu {
    position: static;
    background-color: inherit;
    border-radius: none;
    display: flex;
    gap: 2rem;
  }
}
.navbar__item:not(:last-child) {
  border-bottom: 1px solid var(--clr-border);
}
@media (min-width: 45rem) {
  .navbar__item:not(:last-child) {
    border: none;
  }
}
.navbar__link {
  color: var(--clr-txt-1);
  font-size: 1.125rem;
  font-weight: 500;
  display: block;
  padding: 1.5rem;
  position: relative;
}
@media (min-width: 45rem) {
  .navbar__link {
    color: var(--clr-neutral-1);
    font-size: 0.8125rem;
    padding: 0;
    display: inline-block;
  }
}
.navbar__link:hover::after {
  width: 100%;
}
.navbar__link::after {
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  transform: translateX(-50%);
  height: 1px;
  width: 0;
  background-color: var(--clr-neutral-1);
  transition: all 200ms linear;
}
.navbar__overlay {
  display: none;
  position: fixed;
  inset: 0;
  pointer-events: none;
  user-select: none;
  background: linear-gradient(180deg, var(--clr-menu-bg-1) 0%, var(--clr-menu-bg-2) 100%);
}

.btn__cta {
  background-color: var(--clr-accent);
  border: none;
  border-radius: 2.09375rem;
  color: var(--clr-neutral-1);
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.223125;
  transition: background-color 250ms ease;
  cursor: pointer;
}
.btn__cta:hover {
  background-color: var(--clr-accent-dark);
}
.btn__cta--l {
  padding: 1.0625rem 2.5rem 1.1875rem;
}
.btn__cta--m {
  padding: 0.9375rem 2.0625rem 1rem;
}
.btn__cta--s {
  padding: 0.9375rem 1.4375rem 1rem;
}

.btn__bookmark {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.btn__bookmark svg circle {
  transition: all 250ms ease;
}
.btn__bookmark:hover svg circle {
  fill: var(--clr-btn-hover);
}
@media (min-width: 45rem) {
  .btn__bookmark {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding-right: 1.5rem;
    background-color: var(--clr-neutral-6);
    border-radius: 2.09375rem;
    color: var(--clr-txt-2);
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.223125;
  }
  .btn__bookmark::after {
    content: "Bookmark";
  }
}
.btn__bookmark[data-bookmarked=true] {
  color: var(--clr-accent-dark);
}
.btn__bookmark[data-bookmarked=true] svg circle {
  fill: var(--clr-accent-dark);
}
@media (min-width: 45rem) {
  .btn__bookmark[data-bookmarked=true]::after {
    content: "Bookmarked";
  }
}

.progressbar {
  border: none;
  border-radius: 2.09375rem;
  width: 100%;
  height: 0.75rem;
  background-color: var(--clr-neutral-6);
  accent-color: var(--clr-accent);
}
.progressbar::-webkit-progress-bar {
  background-color: var(--clr-neutral-6);
  border-radius: 2.09375rem;
  accent-color: var(--clr-accent);
}
.progressbar::-moz-progress-bar {
  background-color: var(--clr-accent);
  border-radius: 2.09375rem;
}
.progressbar::-webkit-progress-value {
  background-color: var(--clr-accent);
  border-radius: 2.09375rem;
}

body {
  background: var(--clr-neutral-2);
}

.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}

.hero__img {
  max-width: 90rem;
  margin-inline: auto;
}
.hero__text {
  position: relative;
  background: var(--clr-neutral-1);
  padding: 3.125rem 1.5rem 2.5rem;
  text-align: center;
  border-radius: 0.5rem;
  margin-top: -3.5rem;
  border: 1px solid var(--clr-neutral-4);
}
@media (min-width: 45rem) {
  .hero__text {
    padding: 3.5rem 3rem 3rem;
    margin-top: -5.75rem;
  }
}
.hero__brand {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, 50%);
}
.hero__title {
  margin-bottom: 1rem;
}
@media (min-width: 45rem) {
  .hero__title {
    margin-bottom: 0.9375rem;
  }
}
.hero__description {
  margin-bottom: 1rem;
}
@media (min-width: 45rem) {
  .hero__description {
    margin-bottom: 2.5rem;
  }
}
.hero__buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5625rem;
}

.stats {
  border-radius: 0.5rem;
  background-color: var(--clr-neutral-1);
  padding: 2rem 1.4375rem 2.5rem;
  margin-block: 1.5rem;
  border: 1px solid var(--clr-neutral-6);
  text-align: center;
}
@media (min-width: 45rem) {
  .stats {
    padding: 3rem;
    text-align: left;
  }
}
@media (min-width: 45rem) {
  .stats__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.stats__item {
  position: relative;
}
.stats__item:not(:last-of-type) {
  margin-bottom: 3.0625rem;
}
@media (min-width: 45rem) {
  .stats__item:not(:last-of-type) {
    margin-bottom: 0;
  }
}
.stats__item:not(:last-of-type)::after {
  content: "";
  position: absolute;
  bottom: -1.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 5rem;
  height: 0.0625rem;
  background-color: var(--clr-txt-1);
  opacity: 0.15;
}
@media (min-width: 45rem) {
  .stats__item:not(:last-of-type)::after {
    bottom: auto;
    left: auto;
    right: 0.625rem;
    top: 2rem;
    transform: rotate(90deg);
    width: 3.9375rem;
  }
}
.stats__value {
  display: block;
  color: var(--clr-txt-1);
  font-size: 2rem;
  line-height: 1.223125;
  font-weight: 700;
  margin-bottom: 0.5625rem;
}
.stats__progressbar {
  margin-top: 2rem;
}
@media (min-width: 45rem) {
  .stats__progressbar {
    margin-top: 2.3125rem;
  }
}